import React from "react";
import logo from "./logo.svg";
import styles from "./App.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mode: "normal",
      max_digit: 6,
      min_digit: 5,
      list: [],
      number: 0,
      str_number: 0,
      answerja: "ボタンを押すと日本語読みの答えが表示されます",
      answeren: "ボタンを押すと英語読みの答えが表示されます",
    };

    this.modeChange = this.modeChange.bind(this);
    this.min_digit_Change = this.min_digit_Change.bind(this);
    this.max_digit_Change = this.max_digit_Change.bind(this);
  }

  modeChange(event) {
    this.setState({ mode: event.target.value });
  }
  min_digit_Change(event) {
    this.setState({ min_digit: event.target.value });
  }
  max_digit_Change(event) {
    this.setState({ max_digit: event.target.value });
  }

  handleUpdate(event) {
    var min = Number(this.state.min_digit);
    var max = Number(this.state.max_digit);
    if (min <= max) {
      var digit = Math.floor(Math.random() * (max + 1 - min)) + min;
      var list = [];
      list.push(Math.floor(Math.random() * (9 - 1)) + 1);
      for (var i = 1; i <= digit - 1; i++) {
        list.push(Math.floor(Math.random() * 9));
      }
      var num = Number(list.join(""));

      var str_list = [];
      var comma = [
        "",
        "",
        "",
        ",",
        "",
        "",
        ",",
        "",
        "",
        ",",
        "",
        "",
        ",",
        "",
        "",
        ",",
      ];

      for (var i = 1; i <= digit; i++) {
        str_list.unshift(comma[i - 1]);
        str_list.unshift(list[digit - i]);
      }

      this.setState({
        list: list,
        number: num,
        str_number: str_list.join(""),
        answerja: "ボタンを押すと日本語読みの答えが表示されます",
        answeren: "ボタンを押すと英語読みの答えが表示されます",
      });
    } else {
      /*error message*/
      this.setState({
        str_number: "最小桁数は最大桁数以下にしてください",
        list: [],
        answerja: "ボタンを押すと日本語読みの答えが表示されます",
        answeren: "ボタンを押すと英語読みの答えが表示されます",
      });
    }

    /*
    console.log("You chose: " +this.state.mode +" " +this.state.min_digit +" " +this.state.max_digit +" " +this.state.number);*/
  }

  handleAnswer(event) {
    /*日本語読みの答え*/
    var jadigit = [
      "",
      "",
      "",
      "",
      "",
      "万",
      "",
      "",
      "",
      "億",
      "",
      "",
      "",
      "兆",
    ];
    var ja_answer_list = [];

    for (let i = 1; this.state.list.length >= i; i++) {
      ja_answer_list.unshift(jadigit[i]);
      ja_answer_list.unshift(this.state.list[this.state.list.length - i]);
    }

    /*英語読みの答え */
    var endigit = [
      "",
      "",
      "",
      "",
      "thousand ",
      "",
      "",
      "million ",
      "",
      "",
      "billion ",
      "",
      "",
      "trillion ",
    ];
    var en_answer_list = [];

    for (let i = 1; this.state.list.length >= i; i++) {
      en_answer_list.unshift(endigit[i]);
      en_answer_list.unshift(this.state.list[this.state.list.length - i]);
    }

    /*setState*/
    this.setState({
      answerja: ja_answer_list.join(""),
      answeren: en_answer_list.join(""),
    });
  }

  render() {
    return (
      <div className='app'>
        {/*設定 */}
        <div className='settings'>
          {/*モード*/}
          <div className='mode'>
            <h3>モード</h3>
            <form>
              <select value={this.state.value} onChange={this.modeChange}>
                <option value='normal'>Normal</option>
                <option value='multiply'>Multiply(development now)</option>
                <option value='addtion'>Addition(development now)</option>
                <option value='subtraction'>
                  Subtraction(development now)
                </option>
              </select>
            </form>
          </div>

          {/*最小桁数設定フォーム*/}
          <div className='min_digit'>
            <h3>最小桁数</h3>
            <form>
              <select
                value={this.state.min_digit}
                onChange={this.min_digit_Change}
              >
                <option value='5'>5 (10,000)</option>
                <option value='6'>6 (100,000)</option>
                <option value='7'>7 (1,000,000)</option>
                <option value='8'>8 (10,000,000)</option>
                <option value='9'>9 (100,000,000)</option>
                <option value='10'>10 (1,000,000,000)</option>
                <option value='11'>11 (10,000,000,000)</option>
                <option value='12'>12 (100,000,000,000)</option>
                <option value='13'>13 (1,000,000,000,000)</option>
                <option value='14'>14 (10,000,000,000,000)</option>
                <option value='15'>15 (100,000,000,000,000)</option>
              </select>
            </form>
          </div>

          {/*最大桁数設定フォーム*/}
          <div className='max_digit'>
            <h3>最大桁数</h3>
            <form>
              <select
                value={this.state.max_digit}
                onChange={this.max_digit_Change}
              >
                <option value='6'>6 (100,000)</option>
                <option value='7'>7 (1,000,000)</option>
                <option value='8'>8 (10,000,000)</option>
                <option value='9'>9 (100,000,000)</option>
                <option value='10'>10 (1,000,000,000)</option>
                <option value='11'>11 (10,000,000,000)</option>
                <option value='12'>12 (100,000,000,000)</option>
                <option value='13'>13 (1,000,000,000,000)</option>
                <option value='14'>14 (10,000,000,000,000)</option>
                <option value='15'>15 (100,000,000,000,000)</option>
                <option value='16'>16 (1000,000,000,000,000)</option>
              </select>
            </form>
          </div>
        </div>

        {/*値更新ボタン*/}
        <button
          onClick={() => {
            this.handleUpdate();
          }}
          className='update'
        >
          値を更新
        </button>

        {/*大きい桁の数の表示場所*/}
        <div className='number'>
          <h4>{this.state.str_number}</h4>
        </div>

        {/*答えを表示させるボタン*/}
        <button
          onClick={() => {
            this.handleAnswer();
          }}
          className='answer_btn'
        >
          答えを表示
        </button>

        {/*答え*/}
        <div className='answer'>
          <h4>{this.state.answeren}</h4>
          <h4>{this.state.answerja}</h4>
        </div>
      </div>
    );
  }
}

export default App;
